.SingleControl {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 5px 25px;
}

.SingleControl button {
    display: block;
    font: inherit;
    padding: 5px;
    margin: 0 5px;
    width: 80px;
    border: 1px solid #AA6817;
    cursor: pointer;
    outline: none;
}


.SingleControl button:disabled {
    background-color: #AC9980;
    border: 1px solid rgb(133, 127, 120);
    color: #ccc;
    cursor: default;
}

.SingleControl button:hover:disabled {
    background-color: #AC9980;
    color: #ccc;
    cursor: not-allowed;
}

.Label {
    padding: 10px;
    font-weight: bold;
    width: 80px;
}

.SingleControl .Less {  
    background-color: #D39952;
    color: white;
}

.SingleControl .More {
    background-color: #8F5E1E;
    color: white;
}

.SingleControl .Less:hover, .SingleControl .Less:active {  
    background-color: #DAA972;
    color: white;
}

.SingleControl .More:hover,.SingleControl .More:active {
    background-color: #99703F;
    color: white;
}