.item {
    margin: 10px 0;
    box-sizing: border-box;
    display: block;
    width: 100%;
}

.item a {
    color: #8F5c2c;
    text-decoration: none;
    width: 100%;
    box-sizing: border-box;
    display: block;
}

.item a:hover,
.item a:active,
.item a.active {
    color: #40a4c8;
}

@media (min-width: 500px) {
.item {
    margin:0;
    display: flex;
    height: 100%;
    width: auto;
    align-items:center;
}

.item a {
    color: white;
    text-decoration: none;
    height: 100%;
    padding: 16px 10px;
    border-bottom: 4px solid transparent;
    box-sizing: border-box;
    display: block;
}

.item a:hover,
.item a:active,
.item a.active {
    background-color: #8f5c2c;
    border-bottom: 4px solid #40a4c8;
    color:white;
}

}